import { Map } from 'immutable';

import { NETWORK, NIGHTLY, FALCON, TESTNET, MAINNET } from './contracts';

export enum Token {
    // BTC = 0x00000000,
    ETH = 0x00000001,
    DGX = 0x00000100,
    REN = 0x00010000,
    ABC = 0x00010001,
    XYZ = 0x00010002,
}

// For iterating over Tokens
export const Tokens: Token[] = [Token.ETH, Token.REN, Token.DGX, Token.ABC, Token.XYZ];

export interface TokenDetail {
    name: string,
    symbol: string,
    icon: string,
    address: string,
    digits: number,
}

export let TokenDetails: Map<Token, TokenDetail> = Map();

const addresses = {
    [NIGHTLY]: {
        [Token.DGX]: "0x092ece29781777604afac04887af30042c3bc5df",
        [Token.REN]: "0x15f692d6b9ba8cec643c7d16909e8acdec431bf6",
        [Token.ABC]: "0x49fa7a3b9705fa8deb135b7ba64c2ab00ab915a1",
        [Token.XYZ]: "0x6662449d05312afe0ca147db6eb155641077883f",
    },
    [FALCON]: {
        [Token.DGX]: "0xf4faf1b22cee0a024ad6b12bb29ec0e13f5827c2",
        [Token.REN]: "0x87e83f957a2f3a2e5fe16d5c6b22e38fd28bdc06",
        [Token.ABC]: "0xc96884276d70a1176b2fe102469348d224b0a1fa",
        [Token.XYZ]: "0x8a4a68db5ad08c215c6078111be8793843a53302",
    },
    [TESTNET]: {
        [Token.DGX]: "0x0798297a11cefef7479e40e67839fee3c025691e",
        [Token.REN]: "0x6f429121a3bd3e6c1c17edbc676eec44cf117faf",
        [Token.ABC]: "0xc65d2e9c8924d4848935f4f22e3deca78c5217e5",
        [Token.XYZ]: "0x5753addcd942b495b7297cbfc240a24ba7058274",
    },
    [MAINNET]: {
        [Token.DGX]: "",
        [Token.REN]: "",
        [Token.ABC]: "",
        [Token.XYZ]: "",
    }
};

TokenDetails = TokenDetails.set(Token.ETH, {
    name: 'Ethereum',
    symbol: 'ETH',
    icon: 'eth.svg',
    address: '0xEeeeeEeeeEeEeeEeEeEeeEEEeeeeEeeeeeeeEEeE',
    digits: 18,
});

TokenDetails = TokenDetails.set(Token.DGX, {
    name: 'Digix Gold Token',
    symbol: 'DGX',
    icon: 'dgx.png',
    address: addresses[NETWORK][Token.DGX]!,
    digits: 9,
});

TokenDetails = TokenDetails.set(Token.REN, {
    name: 'Republic Token',
    symbol: 'REN',
    icon: 'ren.svg',
    address: addresses[NETWORK][Token.REN],
    digits: 18,
});

TokenDetails = TokenDetails.set(Token.ABC, {
    name: 'ABC Test Token',
    symbol: 'ABC',
    icon: 'abc.svg',
    address: addresses[NETWORK][Token.ABC]!,
    digits: 12,
});

TokenDetails = TokenDetails.set(Token.XYZ, {
    name: 'XYZ Test Token',
    symbol: 'XYZ',
    icon: 'xyz.svg',
    address: addresses[NETWORK][Token.XYZ]!,
    digits: 18,
});


