import * as React from 'react';

import Web3 from 'web3';
import axios from 'axios';
import { StatusBlock } from './StatusBlock';
import { Topup } from './Topup';
import { TokenBalance } from './TokenBalance';
import { FeesBlock } from './FeesBlock';

import { DarknodeRegistryContract, RewardVaultContract, URLS, NETWORK, NETWORK_DETAILS } from '../lib/contracts';
import { Token, Tokens, TokenDetails } from '../lib/tokens';
import Header from "./Header";
import Loading from "./Loading";

interface StatusProps {
    match: any;
}

interface StatusState {
    network: string;
    multiAddress: string;
    darknodeAddress: string;
    publicKey: string;
    peers: number;
    success: boolean;
    regoStatus: string;
    minBond: number;
    balances: BalanceItem[];
    currentBalance: number;
    error: boolean;
    errorMessage: JSX.Element | null;
    refreshing: boolean;
    correctNetwork: boolean;
}

interface BalanceItem {
    token: Token;
    balance: string;
}

const HEROKU_ADDRESS = 'https://darknode-proxy-api.herokuapp.com/status';

export class StatusPage extends React.Component<StatusProps, StatusState> {
    private web3: Web3;
    private balanceInterval: any;

    constructor(props: StatusProps) {
        super(props);
        this.state = {
            network: '',
            multiAddress: '',
            darknodeAddress: '',
            publicKey: '',
            peers: 0,
            success: false,
            regoStatus: '',
            minBond: 0,
            balances: [],
            currentBalance: 0,
            error: false,
            errorMessage: null,
            refreshing: false,
            correctNetwork: true
        }
        this.refreshInfo = this.refreshInfo.bind(this);
    }

    public async componentDidMount(): Promise<void> {
        if (!this.isValidIP(this.props.match.params.address)) {
            return
        }
        // Retrieve darknode details.
        const webaddr = `${HEROKU_ADDRESS}/${this.props.match.params.address}`;
        axios.get(webaddr)
            .then(async (response) => {
                // Retrieve Web3 from provider otherwise instantiate using Infura.
                const win = (window as any);
                if (typeof win.web3 !== 'undefined') {
                    this.web3 = new Web3(win.web3.currentProvider);
                    const network = await (this.web3.eth.net as any).getNetworkType();
                    if (network !== NETWORK_DETAILS[NETWORK].EthereumNetwork) {
                        this.setState({ correctNetwork: false });
                    }
                } else {
                    this.web3 = new Web3(new Web3.providers.HttpProvider(URLS[NETWORK].Infura));
                }

                Promise.all(
                    [
                        this.getMinBond(),
                        this.getDarknodeStatus(response.data.ethereumAddress),
                        this.getBalances(response.data.ethereumAddress),
                        this.web3.eth.getBalance(response.data.ethereumAddress),
                    ])
                    .then((res) => {
                        const minBond = res[0];
                        const regoStatus = res[1];
                        const balances = res[2];
                        const currentBalance = res[3];

                        this.setState({
                            network: response.data.network,
                            multiAddress: response.data.multiAddress,
                            darknodeAddress: response.data.ethereumAddress,
                            publicKey: response.data.publicKey,
                            peers: response.data.peers,
                            success: true,
                            regoStatus,
                            minBond,
                            balances,
                            currentBalance,
                        }, () => {
                            // Refresh darknode information every 10 seconds.
                            const callRefreshInfo = async () => {
                                try {
                                    await this.refreshInfo();
                                } catch (err) {
                                    console.error(err);
                                }
                                setTimeout(callRefreshInfo, 10 * 1000);
                            };
                            callRefreshInfo().catch(console.error);
                        });
                    })
                    .catch((err) => {
                        console.error(err);
                    });
            })
            .catch((err) => {
                const errorMessage = <p>An error occurred while fetching darknode information from <b>{this.props.match.params.address}</b>. Is there a darknode at that address?</p>;
                this.setState({
                    error: true,
                    errorMessage,
                });
                console.error(err);
            });
    }

    public async componentDidUpdate(): Promise<void> {
        if (!this.balanceInterval && this.state.darknodeAddress) {
            this.balanceInterval = setInterval(async () => {
                const currentBalance = await this.web3.eth.getBalance(this.state.darknodeAddress);
                if (currentBalance !== this.state.currentBalance) {
                    this.setState({ currentBalance });
                }
            }, 5000);
        }
    }

    public render(): JSX.Element {
        return (
            <div className="container">
                <Header />
                {
                    !this.isValidIP(this.props.match.params.address) ?
                        <p>Invalid IP address. <a href="/">Click here</a> to return home.</p>
                        :
                        this.state.error ?
                            this.state.errorMessage
                            :
                            this.state.success && this.web3 !== undefined ?
                                <div className="content">
                                    <div>
                                        <StatusBlock web3={this.web3} regoStatus={this.state.regoStatus} publicKey={this.state.publicKey} network={this.state.network} multiAddress={this.state.multiAddress} darknodeAddress={this.state.darknodeAddress} peers={this.state.peers} minBond={this.state.minBond} />
                                    </div>
                                    <div>
                                        <h1>Balance</h1>
                                        <TokenBalance token={Token.ETH} amount={this.state.currentBalance.toString()} min={0} />
                                        <h1>Top-up Balance</h1>
                                        <Topup web3={this.web3} network={this.state.network} darknodeAddress={this.state.darknodeAddress} />
                                        <h1>Fees Earned</h1>
                                        {
                                            this.state.balances.map((item: BalanceItem, key: number) => {
                                                return (
                                                    <FeesBlock key={key} web3={this.web3} token={item.token} amount={item.balance} darknodeAddress={this.state.darknodeAddress} network={this.state.network} />
                                                );
                                            })
                                        }
                                    </div>
                                </div>
                                : !this.state.correctNetwork ?
                                    <p>Please ensure you are on the {NETWORK_DETAILS[NETWORK].EthereumNetworkLabel} network.</p>
                                    :
                                    <Loading fixed />
                }
            </div>
        );
    }

    private isValidIP(addr: string): boolean {
        return /^(([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\.){3}([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])$/.test(addr);
    }

    private async refreshInfo(): Promise<void> {
        this.setState({ refreshing: true });
        const webaddr = `${HEROKU_ADDRESS}/${this.props.match.params.address}`;
        return await Promise.all(
            [
                axios.get(webaddr),
                this.getDarknodeStatus(this.state.darknodeAddress),
                this.getBalances(this.state.darknodeAddress),
                this.web3.eth.getBalance(this.state.darknodeAddress),
            ])
            .then((res) => {
                const response = res[0];
                const regoStatus = res[1];
                const balances = res[2];
                const currentBalance = res[3];
                this.setState({
                    peers: response.data.peers,
                    regoStatus,
                    balances,
                    currentBalance,
                    refreshing: false
                });
            })
            .catch((err) => {
                console.error(err);
            });
    }

    private async getBalances(ethAddr: string): Promise<BalanceItem[]> {
        const contract = new this.web3.eth.Contract(RewardVaultContract.ABI, RewardVaultContract.address);
        const balances = Tokens.map(async (token: Token) => {
            const tokenDetails = TokenDetails.get(token);
            const balance = await contract.methods.darknodeBalances(ethAddr, tokenDetails.address).call();
            return {
                token,
                balance
            }
        })
        const res = await Promise.all(balances);
        return res;
    }

    private async getDarknodeStatus(darknodeAddress: string): Promise<string> {
        const contract = new this.web3.eth.Contract(DarknodeRegistryContract.ABI, DarknodeRegistryContract.address);
        return new Promise<string>((resolve) => {
            Promise.all([
                contract.methods.isUnregistered(darknodeAddress).call(),
                contract.methods.isRegistered(darknodeAddress).call(),
                contract.methods.canDeregister(darknodeAddress).call(),
                contract.methods.isDeregistered(darknodeAddress).call(),
            ]).then((response) => {
                const res = {
                    isUnreg: response[0],
                    isReg: response[1],
                    canDereg: response[2],
                    isDereg: response[3],
                };
                let regoStatus = '';
                if (res.isUnreg) {
                    regoStatus = 'unregistered';
                } else if (!res.isUnreg && !res.isReg && !res.canDereg && !res.isDereg) {
                    regoStatus = 'regoPending';
                } else if (res.isReg && res.canDereg) {
                    regoStatus = 'registered';
                } else if (res.isReg && !res.canDereg) {
                    regoStatus = 'deregPending';
                } else if (res.isDereg) {
                    regoStatus = 'awaitingRefund';
                }
                resolve(regoStatus);
            });
        });
    }

    private async getMinBond(): Promise<number> {
        const dnrContract = new this.web3.eth.Contract(DarknodeRegistryContract.ABI, DarknodeRegistryContract.address);
        return dnrContract.methods.minimumBond().call();
    }
}



