import * as React from "react";

import Web3 from 'web3';

import { DarknodeRegistryContract } from '../lib/contracts';
import { ERC20Contract } from '../lib/contracts';
import { Token, TokenDetails } from "../lib/tokens";

export const ERROR_UNLOCK_METAMASK = 'Please unlock your MetaMask wallet.';
export const ERROR_TRANSACTION_FAILED = 'Transaction failed, please try again.';

const BUTTON_PENDING = 'Pending...';
const BUTTON_APPROVE = 'Approve';
const BUTTON_REGISTER = 'Register';
const BUTTON_DEREGISTER = 'Deregister';
const BUTTON_REFUND = 'Refund Bond';

interface RegoProps {
    web3: Web3;
    minBond: number;
    regoStatus: string;
    network: string;
    darknodeAddress: string;
    publicKey: string;
}

interface RegoState {
    buttonText: string;
    disabled: boolean;
    errorMessage: string;
}

const RegoStatus = {
    'unregistered': 'Unregistered',
    'regoPending': 'Pending registration (waiting for epoch)',
    'registered': 'Registered',
    'deregPending': 'Pending deregistration (waiting for epoch)',
    'awaitingRefund': 'Awaiting refund',
}

export class Rego extends React.Component<RegoProps, RegoState> {
    constructor(props: RegoProps) {
        super(props);
        this.state = {
            buttonText: '',
            disabled: true,
            errorMessage: '',
        }
        this.handleClick = this.handleClick.bind(this);
        this.approveNode = this.approveNode.bind(this);
        this.registerNode = this.registerNode.bind(this);
        this.deregisterNode = this.deregisterNode.bind(this);
        this.refundNode = this.refundNode.bind(this);
    }

    public componentDidMount(): void {
        this.updateStatus(this.props);
    }

    public componentWillReceiveProps(nextProps: RegoProps): void {
        if (this.props.regoStatus === nextProps.regoStatus) {
            return;
        }
        this.updateStatus(nextProps);
    }

    public render(): JSX.Element {
        const { buttonText, disabled, errorMessage } = this.state;
        const buttonClass = buttonText === BUTTON_DEREGISTER ? 'red' : 'green';
        return (
            <div className="status">
                <span className="status--title">Status:</span> <span>{RegoStatus[this.props.regoStatus]}</span>
                {buttonText ?
                    <button className={`${buttonClass} hover`} onClick={this.handleClick} disabled={disabled}>
                        <span>{buttonText}</span>
                    </button>
                    :
                    <button disabled>Loading...</button>
                }
                {errorMessage &&
                    <p className="warning">{errorMessage}</p>
                }
            </div>
        );
    }

    private async updateStatus(props: RegoProps): Promise<void> {
        let buttonText = BUTTON_PENDING;
        let disabled = true;
        if (props.regoStatus === 'unregistered') {
            const { web3, minBond } = this.props;
            const ethAddress = await web3.eth.getAccounts();
            if (ethAddress[0]) {
                const renAddr = TokenDetails.get(Token.REN).address;
                const ercContract = new web3.eth.Contract(ERC20Contract.ABI, renAddr);
                const allowed = await ercContract.methods.allowance(ethAddress[0], DarknodeRegistryContract.address).call();
                if (allowed < minBond) {
                    buttonText = BUTTON_APPROVE;
                } else {
                    buttonText = BUTTON_REGISTER;
                }
                disabled = false;
            }
        } else if (props.regoStatus === 'registered') {
            buttonText = BUTTON_DEREGISTER;
            disabled = false;
        } else if (props.regoStatus === 'awaitingRefund') {
            buttonText = BUTTON_REFUND;
            disabled = false;
        }
        this.setState({ buttonText, disabled });
    }

    private async handleClick(): Promise<void> {
        const { buttonText, disabled } = this.state;
        this.setState({ buttonText: BUTTON_PENDING, disabled: true, errorMessage: '' });

        let error;
        switch (buttonText) {
            case BUTTON_APPROVE:
                error = await this.approveNode();
                break;
            case BUTTON_REGISTER:
                error = await this.registerNode();
                break;
            case BUTTON_DEREGISTER:
                error = await this.deregisterNode();
                break;
            case BUTTON_REFUND:
                error = await this.refundNode();
                break;
        }
        if (error) {
            this.setState({ buttonText, disabled, errorMessage: error.message });
            return;
        }
    }

    private async approveNode(): Promise<Error | null> {
        const { web3, minBond } = this.props;
        const ethAddress = await web3.eth.getAccounts();
        if (!ethAddress[0]) {
            return new Error(ERROR_UNLOCK_METAMASK);
        }

        const renAddr = TokenDetails.get(Token.REN).address;
        const ercContract = new web3.eth.Contract(ERC20Contract.ABI, renAddr);
        const ercBalance = await ercContract.methods.balanceOf(ethAddress[0]).call();
        if (ercBalance < minBond) {
            return new Error('You do not have sufficient REN to register this node.');
        }
        try {
            await ercContract.methods.approve(DarknodeRegistryContract.address, this.props.minBond).send({ from: ethAddress[0] });
        } catch (error) {
            return new Error(ERROR_TRANSACTION_FAILED);
        }
        return null;
    }

    private async registerNode(): Promise<Error | null> {
        const { web3, minBond, darknodeAddress, publicKey } = this.props;
        const ethAddress = await web3.eth.getAccounts();
        if (!ethAddress[0]) {
            return new Error(ERROR_UNLOCK_METAMASK);
        }

        const dnrContract = new web3.eth.Contract(DarknodeRegistryContract.ABI, DarknodeRegistryContract.address);
        try {
            await dnrContract.methods.register(darknodeAddress, publicKey, minBond).send({ from: ethAddress[0] });
        } catch (error) {
            return new Error(ERROR_TRANSACTION_FAILED);
        }
        return null;
    }

    private async deregisterNode(): Promise<Error | null> {
        if (this.props.regoStatus !== 'registered') {
            return new Error('Only registered nodes can be deregistered.');
        }
        // The node has been registered and can be deregistered.
        const ethAddress = await this.props.web3.eth.getAccounts();
        if (!ethAddress[0]) {
            return new Error(ERROR_UNLOCK_METAMASK);
        }
        const dnrContract = new this.props.web3.eth.Contract(DarknodeRegistryContract.ABI, DarknodeRegistryContract.address);
        const owner = await dnrContract.methods.getOwner(this.props.darknodeAddress).call();
        if (owner !== ethAddress[0]) {
            return new Error('Only the owner can deregister this node.');
        }
        try {
            await dnrContract.methods.deregister(this.props.darknodeAddress).send({ from: ethAddress[0] });
        } catch (error) {
            return new Error(ERROR_TRANSACTION_FAILED);
        }
        return null;
    }

    private async refundNode(): Promise<Error | null> {
        if (this.props.regoStatus !== 'awaitingRefund') {
            return new Error('The bond for this node cannot be refunded at this stage.');
        }
        // The node is awaiting refund.
        const ethAddress = await this.props.web3.eth.getAccounts();
        if (!ethAddress[0]) {
            return new Error(ERROR_UNLOCK_METAMASK);
        }
        const dnrContract = new this.props.web3.eth.Contract(DarknodeRegistryContract.ABI, DarknodeRegistryContract.address);
        const owner = await dnrContract.methods.getOwner(this.props.darknodeAddress).call();
        if (owner !== ethAddress[0]) {
            return new Error('Only the owner can refund the bond for this node.');
        }
        try {
            await dnrContract.methods.refund(this.props.darknodeAddress).send({ from: ethAddress[0] });
        } catch (error) {
            return new Error(ERROR_TRANSACTION_FAILED);
        }
        return null;
    }
}



